.Intro {
  background-color: #ff6961;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  // padding: 0px 10px;
}
.nameSvg path:nth-child(2) {
  stroke-dasharray: 400px;
  stroke-dashoffset: 400px;
  animation: line-anim 3s ease forwards;
}
.nameSvg path:nth-child(3) {
  stroke-dasharray: 490px;
  stroke-dashoffset: 490px;
  animation: line-anim 4s ease forwards;
}
.nameSvg path:nth-child(4) {
  stroke-dasharray: 390px;
  stroke-dashoffset: 390px;
  animation: line-anim 4s ease forwards;
}
.nameSvg path:nth-child(5) {
  stroke-dasharray: 690px;
  stroke-dashoffset: 690px;
  animation: line-anim 4s ease forwards;
}
.nameSvg path:nth-child(6) {
  stroke-dasharray: 330px;
  stroke-dashoffset: 330px;
  animation: line-anim 4s ease forwards;
}
.nameSvg path:nth-child(7) {
  stroke-dasharray: 365px;
  stroke-dashoffset: 365px;
  animation: line-anim 4s ease forwards;
}
.nameSvg path:nth-child(8) {
  stroke-dasharray: 450px;
  stroke-dashoffset: 450px;
  animation: line-anim 4s ease forwards;
}
@keyframes line-anim {
  to {
    stroke-dashoffset: 0;
  }
}
