@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    overflow-y: hidden;
}

.Intro{background-color:#ff6961;width:60%;display:flex;justify-content:center;align-items:center}.nameSvg path:nth-child(2){stroke-dasharray:400px;stroke-dashoffset:400px;-webkit-animation:line-anim 3s ease forwards;animation:line-anim 3s ease forwards}.nameSvg path:nth-child(3){stroke-dasharray:490px;stroke-dashoffset:490px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(4){stroke-dasharray:390px;stroke-dashoffset:390px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(5){stroke-dasharray:690px;stroke-dashoffset:690px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(6){stroke-dasharray:330px;stroke-dashoffset:330px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(7){stroke-dasharray:365px;stroke-dashoffset:365px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(8){stroke-dasharray:450px;stroke-dashoffset:450px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}@-webkit-keyframes line-anim{to{stroke-dashoffset:0}}@keyframes line-anim{to{stroke-dashoffset:0}}

*{padding:0;margin:0}a,a:link,a:visited,a:hover,a:active{color:inherit;text-decoration:inherit;font-weight:inherit}.Home{display:flex;height:100vh;align-items:center;justify-content:center;background-color:#ff6961}.nav{padding:0px;display:flex;align-items:center;justify-content:center;flex-direction:column}.nav .navItem{color:white;display:flex;width:100%;justify-content:space-around}.nav .navItem p{font-weight:lighter}.nav .navItem p:hover{-webkit-text-decoration:underline solid white;text-decoration:underline solid white;cursor:pointer;line-height:10px}

.About{padding:0 100px;padding-top:100px;background-color:#08080dd8;color:white;height:100vh}h2{font-family:"Poppins", sans-serif;font-weight:Bold 700;font-size:40px;padding:0px}hr{width:25px;height:3px;border:1px solid #ff6961;background-color:#ff6961}.socialsWrapper{padding-top:5px}.topBar{display:flex}.topBarSecond{display:flex;flex-direction:column}.topBarSecond-first{display:flex;padding-top:0.5em;justify-content:space-between}.topBarSecond-first p{padding-right:20px}.topBarSecond-first p:hover{-webkit-text-decoration:underline solid #ff6961;text-decoration:underline solid #ff6961;cursor:pointer}.introduction{padding:25px 0;font-family:"Share Tech", sans-serif;font-weight:lighter;line-height:20px;color:white}#paragraph{max-width:450px}.skater{width:100%}.socials{width:1.75%}.learnMore hr{display:none}.learnMore:hover{cursor:pointer}.learnMore:hover hr{display:block}.learnMore .text{display:flex;align-items:center}.learnMore .text p{padding-right:15px;color:#ff6961}.learnMore .text img{width:25px;padding-top:2px}@media (max-width: 600px){.topBar{display:flex;flex-direction:column}h2{font-size:20px}.About{padding:0 50px;padding-top:100px;background-color:#08080dd8;color:white;height:100vh}}


